@media only screen and (max-width: 768px) {
  .img-container {
    width: 100% !important;
    margin-bottom: 1rem !important;
  }

  .img-container:last-child {
    margin-bottom: 3rem !important;
  }
  .img-container img {
    min-height: unset !important;
  }

  .img-container:hover .upper-overlay {
    left: -100% !important;
  }
  .img-container:hover .lower-overlay {
    right: -100% !important;
  }
}

.img-container {
  display: inline-block;
  width: calc(50% - 16px);
  margin-bottom: 4rem;
  position: relative;
  color: white;
  overflow: hidden;
}

.img-container:nth-child(3n + 1) {
  width: 100%;
}

.img-container img {
  width: 100%;
  min-height: 400px;
  max-height: 800px;
}

.upper-overlay {
  position: absolute;
  top: 0%;
  left: -100%;
  width: 100%;
  height: 50%;
  background-color: rgba(24, 24, 27, 0.72);
  transition: left 0.3s ease-in;
}

.upper-overlay-content {
  display: flex;
  font-size: 5rem;
  justify-content: center;
  align-items: center;
  height: 100%;
  font-weight: 700;
  letter-spacing: 8px;
}

.lower-overlay {
  position: absolute;
  top: 50%;
  right: -100%;
  width: 100%;
  height: 50%;
  background-color: rgba(24, 24, 27, 0.72);
  transition: right 0.3s ease-in;
}

.lower-overlay-content {
  padding: 2rem;
  text-align: left;
  display: flex;
  flex-wrap: wrap;
}

.lower-overlay-content .tech-overlay {
  border-radius: 2px;
  border: 1px solid grey;
  padding: 0.25rem 0.5rem;
  margin: 0.25rem;
  margin-left: 0;
  transition: background-color 0.2s ease-in-out;
}

.lower-overlay-content .tech-overlay:hover {
  background-color: grey;
}

.img-container:hover .upper-overlay {
  left: 0%;
}
.img-container:hover .lower-overlay {
  right: 0%;
}
