@media only screen and (max-width: 768px) {
  .business-footer .contact {
    padding: 1rem !important;
    font-size: 2.5rem !important;
    font-weight: 600 !important;
  }

  .business-footer .contact::after {
    width: 100% !important;
    height: 100% !important;
    border-radius: 0% !important;
    top: 0px !important;
    left: 0px !important;
    transition: all 0.1s ease-in;
  }
}

.business-footer {
  margin-top: 2rem;
  overflow: hidden;
  text-align: center;
  cursor: pointer;
}

.business-footer .contact {
  position: relative;
  font-size: 7rem;
  font-weight: 800;
  letter-spacing: -4px;
  padding-top: 5rem;
  padding-bottom: 4rem;
  display: block;
  color: black;
  text-decoration: none;
}

.business-footer .contact::after {
  content: "";
  position: absolute;
  z-index: -1;
  width: 538px;
  height: 400px;
  border-radius: 63% 37% 89% 11% / 70% 37% 63% 30%;
  background-color: rgba(36, 227, 148, 1);
  top: 80px;
  left: 350px;
  transition: all 0.1s ease-in;
}

.business-footer:hover .contact::after {
  content: "";
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
  border-radius: 0px;
}
