@media only screen and (max-width: 768px) {
  .projects-page .page-head .title {
    font-weight: 600 !important;
    font-size: 4rem !important;
    margin-top: 5rem !important;
  }
  .projects-page .page-head::after {
    top: 8% !important;
    right: -2% !important;
    width: 80px !important;
    height: 60px !important;
  }

  .projects-page .page-head::before {
    top: 8% !important;
    right: -1% !important;
    width: 80px !important;
    height: 60px !important;
  }

  .projects-page .page-head .subtitle {
    font-size: 1rem !important;
  }

  .project-list {
    margin-top: 1rem !important;
    min-height: 60vh;
  }

  .search-container .search-input { 
    width: 100% !important;
  }
}

.projects-page {
  text-align: left;
  max-width: 1360px;
  margin: auto;
  padding: 1rem;
}

.projects-page .page-head {
  position: relative;
}

.projects-page .page-head::after {
  content: "";
  position: absolute;
  top: 14%;
  right: -6%;
  width: 280px;
  height: 200px;
  background-image: url("../assets/svg/small-blue.svg");
  background-repeat: no-repeat;
}

.projects-page .page-head::before {
  content: "";
  position: absolute;
  top: 14%;
  right: -3%;
  width: 280px;
  height: 200px;
  background-image: url("../assets/svg/small-green.svg");
  background-repeat: no-repeat;
  z-index: 1;
  opacity: 0.9;
}

.projects-page .page-head .title {
  font-weight: 800;
  font-size: 8rem;
  letter-spacing: -4px;
  margin-top: 8rem;
  margin-bottom: 2rem;
}

.projects-page .page-head .subtitle {
  font-size: 1.5rem;
  max-width: 800px;
}

.project-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-top: 2rem;
}

.error {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.6rem;
}

.error-small {
  box-shadow: 0px 0px 20px #eee;
  text-align: center;
  font-size: 1.3rem;
  margin: auto;
  margin: 2rem 0rem;
  padding: 4rem;
  border-radius: 8px;
}

.search-container {
  padding: 1rem 0rem;
  text-align: right;
  position: relative;
}

.search-container .search-input {
  border: none;
  border-bottom: 2px solid black;
  padding: 0.5rem 1rem;
  padding-left: .2rem;
  padding-right: 1.5rem;
  box-sizing: border-box;
}

.search-icon {
  position: absolute;
  right: 4px;
  top: 22px;
}
.search-icon img{
  width: 16px;
}

.search-container .search-input:focus-visible {
  outline: none;
}

.empty-projects {
  width: 100%;
  padding: 3rem 1rem;
  text-align: center;
}
